@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');
body {
  font-family: "Roboto", sans-serif;
  background: #eee;
  font-size: 0.8rem;
}

table {
  width: 100%;
  border: 1px solid rgba(0,0,0,.12);
  box-shadow: 0 2px 2px 0 rgba(0,0,0,.14), 0 3px 1px -2px rgba(0,0,0,.2), 0 1px 5px 0 rgba(0,0,0,.12);
  margin: 0 0 20px 0;
  background-color: white;

  thead {
    tr {
      border-top: 1px solid rgba(0, 0, 0, 0.12);
      border-bottom: 1px solid rgba(80, 69, 69, 0.12);

      height: 48px;
      th {
        color: rgba(0, 0, 0, 0.54);
        padding: 5px;
        padding-left: 24px;
      }
    }
  }

  tbody {
    tr {
      border-top: 1px solid rgba(0, 0, 0, 0.12);
      border-bottom: 1px solid rgba(80, 69, 69, 0.12);
      height: 48px;
      td {
        padding: 12px 18px;
        height: 48px;
      }
      td:first-of-type {
        padding-left: 24px;
      }
    }
  }
  tbody tr:hover {
    background-color: #f5f5f5;
  }
}

.cursor-pointer {
  cursor: pointer;
}

.extraDetails {
  padding: 15px 5px 5px 0;
  p{
    margin: 0;
    padding: 0;
  }
}

.flag {
  position: absolute;
  right: 8px;
  top: 0px;
  font-size: 1.7rem;
}

.card {
  h5 {
    max-width: 70%;
  }
}
.detail-label {
  font-weight: 500;
}

.bdl {
  border-left: 1px solid rgba(0, 0, 0, 0.12);

}

.bdr {
  border-right: 1px solid rgba(0, 0, 0, 0.12);
}

.text-overlap {
  position: absolute;
  bottom: 5%;
  font-size: 1rem;
  left: 5%;
  color: black;
}
.zoom {
  transition: transform 0.5s;
}
.zoom:hover {
  transform: scale(1.05);
}

.custom-card {
  background-color: #c5c6c7;
}

.loading {
  min-height: 100vh;
  h4 {
    color: #808080;
  }
}